function ContentRegistration() {
  // TODO: Receber dados do Input
  // TODO: Bottão Submit
  // TODO: Enviar dados par ao Banco

  const [registerType, setRegisterType] = useState("Personal");
  const [name, setName] = useState("");
  const [matricula, setMatricula] = useState("");
  const [email, setEmail] = useState("");
  const [rua, setRua] = useState("");
  const [bairro, setBairro] = useState("");
  const [cep, setCep] = useState("");
  const [city, setCity] = useState("");
  const [uf, setUf] = useState("");
  const [complement, setComplement] = useState("");
  const [isPersonal, setIsPersonal] = useState(false);

  const handleRegisterType = (event) => {
    setRegisterType(event.target.value);
    if (registerType === "Personal") setIsPersonal(true);
    else setIsPersonal(false);
  };

  const classes = useStyles();

  return (
    <div>
      <FormControl className={classes.formControl}>
        <InputLabel>Tipo de Registro</InputLabel>
        <Select value={registerType} onChange={handleRegisterType}>
          <MenuItem value={"Aluno"}>Aluno</MenuItem>
          <MenuItem value={"Personal"}>Personal</MenuItem>
        </Select>
      </FormControl>

      <FormControl className={classes.formControl}>
        <TextField id="name" label="Nome" />
      </FormControl>

      <FormControl className={classes.formControl}>
        <TextField id="matricula" label="Matricula" />
      </FormControl>

      <FormControl className={classes.formControl}>
        <TextField id="email" label="Email" />
      </FormControl>

      <FormControl className={classes.formControl}>
        <TextField id="cep" label="CEP" />
      </FormControl>

      <FormControl className={classes.formControl}>
        <TextField id="street" label="Rua" />
      </FormControl>

      <FormControl className={classes.formControl}>
        <TextField id="neighborhood" label="Bairro" />
      </FormControl>

      <FormControl className={classes.formControl}>
        <TextField id="city" label="Cidade" />
      </FormControl>

      <FormControl className={classes.formControl}>
        <TextField id="uf" label="UF" />
      </FormControl>

      <FormControl className={classes.formControl}>
        <TextField id="complement" label="Complemento" />
      </FormControl>

      <FormControl className={classes.formControl}>
        <TextField id="position" label="Cargo" disabled={isPersonal} />
      </FormControl>

      <FormControl className={classes.formControl}>
        <TextField id="salary" label="Salário" disabled={isPersonal} />
      </FormControl>
    </div>
  );
}

