import React, { useState, useEffect, Fragment } from "react";
import clsx from "clsx";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
import Button from "@material-ui/core/Button";
import List from "@material-ui/core/List";
import CssBaseline from "@material-ui/core/CssBaseline";
import Typography from "@material-ui/core/Typography";
import Divider from "@material-ui/core/Divider";
import MenuIcon from "@material-ui/icons/Menu";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import { useHistory } from "react-router-dom";
import IconButton from "@material-ui/core/IconButton";
import Toolbar from "@material-ui/core/Toolbar";
import AppBar from "@material-ui/core/AppBar";
//dark mode

//import { lightTheme, darkTheme } from "./../styles/theme";
import styled from "styled-components";
import { ThemeProvider } from "@material-ui/styles";
import { createMuiTheme } from "@material-ui/core";

const drawerWidth = 240;

var change = false;

export default function Bars(props) {
  //Variables
  const history = useHistory();
  const classes = useStyles();
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);
  const [stateDark, setStateDark] = useState("dark");

  const Content = props.content;
  const listIcons = props.iconsSideBar;
  const listTextDrawer = props.buttonsSideBar;

  useEffect(() => {
    //identifies local theme
    const localTheme = window.localStorage.getItem("theme");
    localTheme && setStateDark(localTheme);
  }, []);

  const toggleTheme = () => {
    if (stateDark === "light") {
      setStateDark("dark");
      window.localStorage.setItem("theme", "dark");
    } else {
      setStateDark("light");
      window.localStorage.setItem("theme", "light");
    }

    console.log(stateDark);
  };

  const handleDrawer = () => {
    change = !change;
    setOpen(change);
  };

  const redirect = (text) => {
    //for set value of variable change, it work in the menu button
    change = false;
    let path = "/" + text;
    history.push(path);
  };

  const darkTheme = createMuiTheme({
    palette: {
      type: stateDark,
    },
  });

  //JSX
  return (
    <div className={classes.root}>
      <CssBaseline />

      <AppBar
        position="fixed"
        className={clsx(classes.appBar, {
          [classes.appBarShift]: open,
        })}
      >
        <Toolbar>
          <Button onClick={handleDrawer} className={classes.menuButton}>
            <MenuIcon />
          </Button>
          <Typography variant="h6" className={classes.titleAppBar}>
            {props.namePage}
          </Typography>
          <Button
            className={classes.buttonDarkMode}
            onClick={toggleTheme}
            variant="contained"
            color="inherit"
            size="small"
            style={{
              backgroundColor: "#111d29",
              border: "2px solid white",
              borderRadius: 10,
            }}
          >
            Dark Mode
          </Button>
        </Toolbar>
      </AppBar>

      <Drawer
        variant="permanent"
        className={clsx(classes.drawer, {
          [classes.drawerOpen]: open,
          [classes.drawerClose]: !open,
        })}
        classes={{
          paper: clsx({
            [classes.drawerOpen]: open,
            [classes.drawerClose]: !open,
          }),
        }}
      >
        <div>
          <List className={classes.listDrawer}>
            {props.routesSideBar.map((text, index) => (
              <ListItem button onClick={() => redirect(text)} key={text}>
                <ListItemIcon className={classes.iconsDrawer}>
                  {listIcons[index]}
                </ListItemIcon>
                <ListItemText
                  primary={listTextDrawer[index]}
                  className={classes.colorComponentsDrawer}
                />
              </ListItem>
            ))}
          </List>
        </div>
      </Drawer>
      <main className={classes.content}>
        <div className={classes.toolbar} />
        <ThemeProvider theme={darkTheme}>
          <CssBaseline />
          <Fragment>
            {
              // the globaltheme search the information about theme in ThemeProvider
            }
            <Container>
              <Content />
            </Container>
          </Fragment>
        </ThemeProvider>
      </main>
    </div>
  );
}

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  appBar: {
    backgroundColor: "#111d29",
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    backgroundColor: "#243e57",
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    color: "#fff",
    position: "absolute",
    left: 0,
  },
  titleAppBar: {
    marginLeft: 50,
  },
  hide: {
    display: "none",
  },
  buttonDarkMode: {
    marginLeft: "auto",
    width: 120,
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: "nowrap",
  },
  drawerOpen: {
    backgroundColor: "#243e57",
    width: drawerWidth,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerClose: {
    backgroundColor: "#111d29",
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),

    overflowX: "hidden",
    width: theme.spacing(7) + 1,
    // [theme.breakpoints.up("sm")]: {
    //   width: theme.spacing(7) + 1,
    // },
  },
  listDrawer: {
    paddingTop: 70,
  },
  toolbar: {
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-end",
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
  iconsDrawer: {
    color: "#fff",
  },
  colorComponentsDrawer: {
    color: "#fff",
  },
}));

const Container = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;
