function ContentSettings() {
  const history = useHistory();

  const redirect = (text) => {
    let path = "/" + text;
    history.push(path);
  };

  return (
    <div>
      <Button
        onClick={() => redirect("contato")}
        variant="contained"
        style={{ backgroundColor: "#ff3838", color: "white" }}
      >
        Contato com o suporte
      </Button>
    </div>
  );
}
