import React, { Component } from "react";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
//import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { withStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import { Link, Redirect } from "react-router-dom";
//import Firebase from "../../db/firebase/Firebase";
import Box from "@material-ui/core/Box";

class Cadaster extends Component {
  constructor(props) {
    super(props);
    this.state = {
      valueEmail: "",
      valuePassword: "",
      isRegistered: false,
    };

    this.handleChangeEmail = this.handleChangeEmail.bind(this);
    this.handleChangePassword = this.handleChangePassword.bind(this);
    this.register = this.register.bind(this);
    this.keyPressed = this.keyPressed.bind(this);
  }

  handleChangeEmail(event) {
    this.setState({ valueEmail: event.target.value });
    console.log("Um email cadastro: " + this.state.valueEmail);
  }

  handleChangePassword(event) {
    this.setState({ valuePassword: event.target.value });
    console.log("Uma senha cadastro: " + this.state.valuePassword);
  }

  register() {
    // console.log("Email cadastrado: " + this.state.valueEmail);
    // console.log("Senha cadastrada: " + this.state.valuePassword);
    // Firebase.auth()
    //   .createUserWithEmailAndPassword(
    //     this.state.valueEmail,
    //     this.state.valuePassword
    //   )
    //   .then((response) => {
    //     this.setState({ isRegistered: true });
    //     console.log(response);
    //   })
    //   .catch((error) => {
    //     this.setState({ isRegistered: false });
    //     console.log(error);
    //   });
    // console.log(this.state.isRegistered);
  }

  keyPressed(event) {
    if (event.which === 13) {
      this.register();
    }
  }

  render() {
    const { classes } = this.props;
    if (this.state.isRegistered) {
      return <Redirect push to="/" />;
    }
    return (
      <div className={classes.screen}>
        <Container component="main" className={classes.cadaster} maxWidth="xs">
          <CssBaseline />
          <Box paddingY={3} className={classes.paper}>
            <Avatar className={classes.avatar}>
              <LockOutlinedIcon />
            </Avatar>
            <Typography component="h1" variant="h5">
              Sign up
            </Typography>
            <form className={classes.form} noValidate>
              <Grid container spacing={2}>
                <Grid item xs={12} sm={6}>
                  <TextField
                    autoComplete="fname"
                    name="firstName"
                    variant="outlined"
                    required
                    fullWidth
                    id="firstName"
                    label="Nome"
                    autoFocus
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    variant="outlined"
                    required
                    fullWidth
                    id="lastName"
                    label="Sobrenome"
                    name="lastName"
                    autoComplete="lname"
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    variant="outlined"
                    required
                    fullWidth
                    id="email"
                    label="Email"
                    name="email"
                    autoComplete="email"
                    onChange={this.handleChangeEmail}
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    variant="outlined"
                    required
                    fullWidth
                    name="password"
                    label="Senha"
                    type="password"
                    id="password"
                    autoComplete="current-password"
                    onChange={this.handleChangePassword}
                    onKeyPress={this.keyPressed}
                  />
                </Grid>
              </Grid>

              <Button
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
                onClick={this.register}
              >
                {" "}
                Sign Up
              </Button>
              <Grid container justify="flex-end">
                <Grid item>
                  <Link to="/" variant="body2">
                    {"Já possui uma conta? Entrar"}
                  </Link>
                </Grid>
              </Grid>
            </form>
          </Box>
        </Container>
      </div>
    );
  }
}

const styles = (theme) => ({
  screen: {
    backgroundColor: "#111d29",
    position: "fixed",
    width: "100%",
    height: "100%",
  },

  cadaster: {
    backgroundColor: "#f5f5f5",
    boxShadow: "1px 1px 3px gray",
    borderRadius: "10px",
    height: "auto",
  },

  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },

  avatar: {
    margin: theme.spacing(1),
    backgroundColor: "#111d29",
  },
  form: {
    width: "100%",
    marginTop: theme.spacing(1),
  },
  submit: {
    background: "#4b5458",
    "&:hover": {
      backgroundColor: "#111d29",
    },
    color: "white",
    height: 48,
    padding: "0px 30px",
    margin: theme.spacing(3, 0, 2),
  },
});

export default withStyles(styles)(Cadaster);
