import React, { Component } from "react";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Grid from "@material-ui/core/Grid";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { withStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import { Link, Redirect } from "react-router-dom";
//import Firebase from "../../db/firebase/Firebase";
import Box from "@material-ui/core/Box";

window.onload = () => window.localStorage.setItem("isAuth", "false");

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      valueEmail: "",
      valuePassword: "",
      isAuthenticated: false,
    };

    this.handleChangeEmail = this.handleChangeEmail.bind(this);
    this.handleChangePassword = this.handleChangePassword.bind(this);
    this.authenticate = this.authenticate.bind(this);
    this.keyPressed = this.keyPressed.bind(this);
  }

  handleChangeEmail(event) {
    this.setState({ valueEmail: event.target.value });
    console.log("Um email: " + this.state.valueEmail);
  }

  handleChangePassword(event) {
    this.setState({ valuePassword: event.target.value });
    console.log("Uma senha: " + this.state.valuePassword);
  }

  authenticate() {
    // console.log("Email enviado: " + this.state.valueEmail);
    // console.log("Senha enviada: " + this.state.valuePassword);
    // Firebase.auth()
    //   .signInWithEmailAndPassword(
    //     this.state.valueEmail,
    //     this.state.valuePassword
    //   )
    //   .then((response) => {
    //     this.setState({ isAuthenticated: true });
    //     window.localStorage.setItem("isAuth", "true");
    //   })
    //   .catch((error) => {
    //     this.setState({ isAuthenticated: false });
    //     window.localStorage.setItem("isAuth", "false");
    //   });
  }

  keyPressed(event) {
    if (event.which === 13) {
      this.authenticate();
    }
  }

  render() {
    const { classes } = this.props;
    if (this.state.isAuthenticated) {
      window.localStorage.setItem("isAuth", "true");
      return <Redirect push to="/dashboard" />;
    }
    return (
      <div className={classes.screen}>
        <Container component="main" className={classes.login} maxWidth="xs">
          <CssBaseline />
          <Box paddingY={3} className={classes.paper}>
            <Avatar className={classes.avatar}>
              <LockOutlinedIcon />
            </Avatar>
            <Typography component="h1" variant="h5" color="action">
              Login
            </Typography>
            <form className={classes.form} Validate>
              <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                id="email"
                label="Email"
                name="email"
                autoComplete="email"
                onChange={this.handleChangeEmail}
                autoFocus
              />
              <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                name="password"
                label="Senha"
                type="password"
                id="password"
                autoComplete="current-password"
                onChange={this.handleChangePassword}
                onKeyPress={this.keyPressed}
              />
              <FormControlLabel
                control={<Checkbox value="remember" color="action" />}
                label="Lembrar me"
              />

              <Button
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
                onClick={this.authenticate}
              >
                entrar
              </Button>
              <Grid container>
                <Grid item xs>
                  <Link to="#" variant="body2">
                    Esqueceu a senha?
                  </Link>
                </Grid>
                <Grid item>
                  <Link to="/cadastro" variant="body2">
                    {"Não possui conta? Crie aqui."}
                  </Link>
                </Grid>
              </Grid>
            </form>
          </Box>
        </Container>
      </div>
    );
  }
}

const styles = (theme) => ({
  screen: {
    backgroundColor: "#111d29",
    position: "fixed",
    width: "100%",
    height: "100%",
  },

  login: {
    backgroundColor: "#f5f5f5",
    boxShadow: "1px 1px 3px gray",
    borderRadius: "10px",
    height: "auto",
  },

  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },

  avatar: {
    // icon
    margin: theme.spacing(1),
    backgroundColor: "#111d29",
  },
  form: {
    width: "100%",
    marginTop: theme.spacing(1),
  },
  submit: {
    // button
    background: "#4b5458",
    "&:hover": {
      backgroundColor: "#111d29",
    },
    color: "white",
    height: 48,
    padding: "0px 30px",
    margin: theme.spacing(3, 0, 2),
  },
});

export default withStyles(styles)(Login);
