import React, { Component } from "react";
import { HashRouter, Switch, Route } from "react-router-dom";
import { Redirect } from "react-router-dom";
import Dashboard from "../pages/dashboard/Dashboard";
import Registration from "../pages/registration/Registration";
import Finance from "../pages/finance/Finance";
import Settings from "../pages/settings/Settings";
import Login from "../pages/login/Login";
import Cadaster from "../pages/cadaster/Cadaster";
import Employee from "../pages/employee/Employee";
import Notification from "../pages/notification/Notification";
import Contact from "../pages/contact/Contact";
import Training from "../pages/training/Training";
import Customers from "../pages/customer/Customer";

const PrivateRoute = ({ component: Component, ...rest }) => {
  var isTrueSet = true;

  return (
    <Route
      {...rest}
      render={(props) =>
        isTrueSet ? <Component {...props} /> : <Redirect to="/" />
      }
    />
  );
};

class Routes extends Component {
  render() {
    return (
      <HashRouter>
        <Switch>
          <Route exact path="/" component={Login}></Route>
          <Route path="/cadastro" component={Cadaster}></Route>
          <PrivateRoute path="/dashboard" component={Dashboard}></PrivateRoute>
          <PrivateRoute path="/treino" component={Training}></PrivateRoute>
          <PrivateRoute path="/clientes" component={Customers}></PrivateRoute>
          <PrivateRoute
            path="/registrar"
            component={Registration}
          ></PrivateRoute>
          <PrivateRoute path="/financeiro" component={Finance}></PrivateRoute>
          <PrivateRoute
            path="/funcionarios"
            component={Employee}
          ></PrivateRoute>
          <PrivateRoute
            path="/notificacoes"
            component={Notification}
          ></PrivateRoute>
          <PrivateRoute
            path="/configuracoes"
            component={Settings}
          ></PrivateRoute>
          <PrivateRoute path="/contato" component={Contact}></PrivateRoute>
        </Switch>
      </HashRouter>
    );
  }
}

export default Routes;
